import React, { useState, useEffect } from "react";
import { useParams, useHistory } from "react-router-dom";
import { getMeal } from "../api";
import { Preloader } from "../components/Preloader";

export function Recipe() {
  const { id } = useParams();
  const [meals, setMeals] = useState([]);
  const { goBack } = useHistory();

  useEffect(() => {
    getMeal(id).then((data) => setMeals(data.meals));
  }, [id]);

  function a() {
    return <div>a!</div>;
  }

  return (
    <>
      {!meals.length ? (
        <Preloader />
      ) : (
        <div>
          <span className="recipe-el-parent">
            <span className="recipe-post intro">Intro</span>
            <h1>{meals[0].strMeal}</h1>
            <ul className="recipe-ul">
              <li>Category: {meals[0].strCategory}</li>
              <li>Area: {meals[0].strArea}</li>
              {/* <li>{meals[0].strTags}</li> */}
            </ul>
          </span>
          <img src={meals[0].strMealThumb} alt={meals[0].strMeal} />

          <div className="recipe-el-parent">
            <span className="recipe-post ingedients">Ingredients</span>
            <h4>You will need:</h4>
            <ol>
              {Object.keys(meals[0]).map((elem) => {
                if (elem.includes("strIngredient") && meals[0][elem]) {
                  if (meals[0][elem] !== ("" || null || undefined)) {
                    return (
                      <li key={elem.slice(13)}>
                        <span className="recipe-span">
                          <h5>{meals[0][elem]} </h5>{" "}
                          {meals[0][`strMeasure${elem.slice(13)}`]}
                        </span>
                        <hr />
                      </li>
                    );
                  } else {
                    return null;
                  }
                } else {
                  return null;
                }
              })}
            </ol>
          </div>

          <div className="recipe-el-parent">
            <span className="recipe-post htm">How to make</span>
            <div>{meals[0].strInstructions}</div>
            {meals[0].strYoutube.length !== 0 ? (
              <iframe
                className="youtube-player"
                type="text/html"
                width="640px"
                height="385px"
                title={meals[0].strMeal}
                src={meals[0].strYoutube.replace("watch?v=", "embed/")}
                allowFullScreen
              ></iframe>
            ) : null}
            {meals[0].strSource.length !== 0 ? (
              <>
                <br />
                <a href={meals[0].strSource} target="_blank" rel="noreferrer">
                  Checkout recipe better{" "}
                  <i className="material-icons">remove_red_eye</i>
                </a>
              </>
            ) : null}
          </div>

          <br />
          <button onClick={goBack} className="btn recipe-leave">
            go back
          </button>
        </div>
      )}
    </>
  );
}
