import React, { useState } from "react";

export function Search({ cb = Function.prototype }) {
  const [value, setValue] = useState("");

  const handleKey = (e) => {
    if (e.key === "Enter") {
      handleSubmit();
    }
  };

  const handleSubmit = () => {
    cb(value);
  };

  return (
    <div className="row">
      <div className="input-field col s12">
        <input
          type="serach"
          placeholder="Search..."
          onKeyDown={handleKey}
          onChange={(e) => setValue(e.target.value)}
          value={value}
          id="search-field"
        />
        <button
          cln="btn"
          style={{ position: "absolute", top: "0", left: "200px" }}
          onClick={handleSubmit}
        >
          Search
        </button>
      </div>
    </div>
  );
}
